<template>
  <div class="pagination-wrap">
    <div class="pagination">
      <div class="pag" :class="{ 'disabled': modelValue == 1 }" @click="prevPage">
        «</div>
      <div class="num-wrap">
        <div class="pag num" v-for="page in totalPages" :key="page" :class="{ 'active': page === modelValue }"
          @click="changePage(page)">
          {{ page === totalPages && totalPages > 3 ? '. . . ' + page : page }}
        </div>
      </div>

      <div class="pag" :class="{ 'disabled': modelValue == totalPages }" @click="nextPage">»</div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    wineAmount: {
      type: Number,
      required: true,
    },
    perPage: {
      type: Number,
      required: true,
    },
    modelValue: {
      type: Number,
      required: true,
    }
  },
  computed: {
    totalPages() {
      return Math.ceil(this.wineAmount / this.perPage)
    },

  },
  methods: {
    changePage(page) {
      this.$emit('update:modelValue', page)
    },
    prevPage() {
      this.$emit('update:modelValue', this.modelValue - 1)
    },
    nextPage() {
      this.$emit('update:modelValue', this.modelValue + 1)
    }
  },

  mounted() {

  }
}
</script>

<style>
.disabled {
  pointer-events: none;
}

.pagination-wrap {
  display: flex;
  justify-content: left;
  max-width: 62%;
  margin-left: auto;
  padding-bottom: 60px;
}

.pagination {
  display: flex;
}

.pagination .pag {
  cursor: pointer;
  color: black;
  align-self: baseline;
  padding: 10px 20px;
  font-size: 28px;
  text-decoration: none;
  transition: background-color .3s;
  margin-right: 15px;
  border-radius: 5px;

}

.num-wrap {
  display: flex;
}

.pagination .pag.active {
  background-color: #998431;
  color: white;
}

.pagination .pag:hover:not(.active) {
  background-color: #ddd;
}

.num {
  display: none;
}

.num:first-child,
.num:last-child,
.num:nth-child(2),
.num.active {
  display: block;
}

@media(max-width:768px) {
  .pagination-wrap {
    max-width: 100%;
    justify-content: center;
  }
}

@media(max-width:480px) {
    .pagination .pag {
    padding: 7px 15px;
    font-size: 20px;
    margin-right: 12px;
  }
}

@media(max-width:360px) {
  .pagination .pag {
    padding: 5px 13px;
    font-size: 18px;
    margin-right: 10px;
  }

}
</style>