import axios from "axios";
export default {
  state: {
    favList: []
  },
  getters: {
    allFavList(state) {
      return state.favList
    }
  },
  mutations: {
    addToFavList(state, list) {
      state.favList = list
    },

  },
  actions: {
    changeFavlist(context, wine) {
      wine.favorite = !wine.favorite
      axios.put(`https://648044caf061e6ec4d48ef0e.mockapi.io/api/wine/wines/${wine.id}`, wine)
        .then(() => {
          axios.get('https://648044caf061e6ec4d48ef0e.mockapi.io/api/wine/wines')
            .then(resp => {
              resp = resp.data.filter(item => item.favorite)
              context.commit('addToFavList', resp)
            })
        })
        .catch((e) => {
          alert(e)
        })
    },
    loadFavList(context) {
      axios.get('https://648044caf061e6ec4d48ef0e.mockapi.io/api/wine/wines')
        .then(resp => {
          resp = resp.data.filter(item => item.favorite)
          context.commit('addToFavList', resp)
        })
        .catch((e) => {
          alert(e)
        })
    }
  }

}