<template>
  <preloaderVue v-if="isWineLoading" />
  <div class="container container-small" v-else>
    <div class="wine-inner">
      <div class="wine-image">
        <img :src="getCurWine.image" :alt="getCurWine.title">
        <button class="like phone-button" @click.prevent="addToFavorite(getCurWine)">
          <img :src="chooseLikeImage" alt="add to favorite">
        </button>
      </div>
      <div class="wine-info">
        <button class="like desctop-button" @click.prevent="addToFavorite(getCurWine)">
          <img :src="chooseLikeImage" alt="add to favorite">
        </button>
        <h1>{{ getCurWine.title }}</h1>
        <ul class="from">
          <li>{{ getCurWine.country }}</li>
          <li v-if="getCurWine.province">{{ getCurWine.province }}</li>
          <li v-if="getCurWine.region_1">{{ getCurWine.region_1 }}</li>
        </ul>
        <div class="winery">
          Winery : {{ getCurWine.winery }}
        </div>
        <div class="variety" v-if="getCurWine.variety">
          Variety : {{ getCurWine.variety }}
        </div>
        <div class="designation" v-if="getCurWine.designation">
          Designation : {{ getCurWine.designation }}
        </div>
        <div class="box">
          <p class="rating">{{ getCurWine.points }}</p>
          <p class="price">Price : {{ getCurWine.price }}$</p>
        </div>
      </div>
    </div>
    <div class="wine-description">
      <div class="descr">
        <p>What does the taster say :</p>
        {{ getCurWine.description }}
        <span v-if="getCurWine.taster_name">©{{ getCurWine.taster_name }}</span>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex';
import preloaderVue from '@/components/base/preloaderVue.vue';
import { likeMixin } from '@/mixins/addFavourite'
export default {
  components: {
    preloaderVue,

  },
  mixins: [likeMixin],
  computed: {
    ...mapGetters(['getCurWine', 'isWineLoading']),
    chooseLikeImage() {
      return this.getCurWine.favorite === false ? require(`@/assets/like.svg`) : require(`@/assets/like.png`)
    }
  },
  mounted() {
    this.$store.dispatch('getWineByID', { id: this.$route.params.id })
  }
}
</script>

<style scoped>
.like {
  position: absolute;
  right: 10px;
  top: 10px;
  border: transparent;
  width: 40px;
  height: 40px;
  background-color: transparent;
  cursor: pointer;
}
.like img{
  width: 100%;
  object-fit: contain;
  min-height: 100%;
}
.container-small {
  max-width: 980px;
}

.wine-inner {
  display: flex;
  justify-content: space-between;
  padding: 40px 0;
}

.wine-image {
  width: 20%;
}

.wine-info {
  width: 75%;
  margin-top: 40px;
  display: flex;
  flex-direction: column;
  position: relative;
}

h1 {
  font-family: Playfair Display;
  font-size: 40px;
  font-weight: 700;
  margin: 25px 0;
  max-width: 90%;
}

.from {
  max-width: 385px;
  font-size: 22px;
  margin: 30px 0;
  padding-left: 25px;
}

.from li {
  margin-bottom: 10px;
}

.box {
  display: flex;
  justify-content: space-between;
  max-width: 500px;
  margin-top: auto;
}

.winery,
.variety,
.designation {
  font-size: 24px;
  margin: 20px 0;
}

.rating {
  font-size: 40px;
  font-weight: 600;
  display: inline-block;
  position: relative;
  margin: 0;
}

.rating::after {
  position: absolute;
  content: '';
  width: 33px;
  height: 33px;
  background-repeat: no-repeat;
  background-size: cover;
  top: 5px;
  right: -38px;
  background-image: url(@/assets/star.png);
}

.price {
  font-size: 32px;
  font-weight: 600;
  margin: 0;
  margin-top: 5px;
}

.descr {
  font-size: 24px;
  font-weight: 500;
  line-height: 1.4;
}

.descr p {
  font-weight: 600;
  display: inline-block;
  margin: 0;
}

.descr span {
  font-size: 20px;
  font-weight: 600;
}

.wine-description {
  padding-bottom: 50px;
}
.phone-button{
  display: none;
}
@media(max-width:768px) {
  .wine-inner {
    align-items: center;
  }

  .wine-image {
    width: 25%;
  }

  .wine-info {
    width: 70%;
  }

  h1 {
    font-size: 32px;
  }

  .from {
    font-size: 20px;
  }

  .winery,
  .variety,
  .designation {
    font-size: 22px;
    margin: 15px 0;
  }

  .rating {
    font-size: 36px;
  }

  .rating::after {
    width: 28px;
    height: 28px;
  }

  .price {
    font-size: 28px;
  }

  .descr {
    font-size: 22px;
  }
}

@media(max-width:480px) {
  .wine-inner {
    flex-direction: column;
  }

  .wine-image {
    text-align: center;
    width: 100%;
    position: relative;
  }

  .wine-info {
    width: 100%;
  }

  .desctop-button {
    display: none;
  }
  .phone-button{
  display: block;
}
}</style>