<template>
  <li name="fade" class="catalog-item">
    <div class="catalog-link">
      <div class="main-image">
        <img :src="wine.image" :alt="wine.title">
      </div>
      <div class="deckription">
        <router-link :to="{ 'name': 'detailPage', params: { color: wine.color, id: wine.id } }" class="title">{{
          wine.title }}</router-link>
        <div class="country-wrap">
          <img class="country-flag" :src="wine.countryImg" alt="flag">
          <h5 class="country">{{ wine.country }}</h5>
        </div>
        <span class="rating">{{ wine.points }}</span>
        <p class="price">Price: {{ wine.price }}$</p>
        <img class="barrel" src="@/assets/grape.png" alt="background image">
        <button class="like" @click.prevent="addToFavorite(wine)">
          <img :src="chooseLikeImage" alt="add to favorite">
        </button>
      </div>
    </div>
  </li>
</template>

<script>
import { mapGetters } from 'vuex'
import { likeMixin } from '@/mixins/addFavourite'
export default {
  props: {
    wine: Object
  },
  mixins: [likeMixin],
  computed: {
    ...mapGetters(['allFavList']),
    chooseLikeImage() {
      return this.wine.favorite === false ? require(`@/assets/like.svg`) : require(`@/assets/like.png`)
    }
  }
}
</script>

<style scoped>
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s ease;
}

.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}

.catalog-item {
  padding: 10px;
  border: 3px solid #C4C4C4;
  list-style-type: none;
  border-radius: 15px;
  margin-bottom: 50px;

}

.catalog-item:hover .main-image img {
  transform: scale(1.3) rotate(20deg);
}

.catalog-link {
  display: flex;
  justify-content: space-between;
  text-decoration: none;
  align-items: center;
}

.main-image {
  max-width: 20%;
  padding: 0 20px;
}

.main-image img {
  transition: all .5s;
  max-height: 400px;
}

.deckription {
  width: 78%;
  position: relative;
}

.barrel {
  content: "";
  width: 40%;
  min-height: 70%;
  position: absolute;
  right: -8%;
  bottom: -14%;
  z-index: 5;
  object-fit: contain;
}

.title {
  font-family: Playfair Display;
  font-size: 36px;
  font-weight: 700;
  margin: 25px 0;
  max-width: 90%;
  display: inline-block;
  text-decoration: none;
}

.country {
  font-size: 25px;
  font-weight: 600;
  padding-bottom: 30px;
  border-bottom: 2px solid #E6E3C4;
  max-width: 50%;
  padding-left: 40px;
  margin: 15px 0;
}

.country-wrap {
  position: relative;
}

.country-flag {
  position: absolute;
  content: '';
  width: 35px;
  height: 35px;
  top: -5px;
  left: -4px;
}

.rating {
  font-size: 40px;
  font-weight: 600;
  display: inline-block;
  margin-bottom: 20px;
  position: relative;
}

.rating::after {
  position: absolute;
  content: '';
  width: 33px;
  height: 33px;
  background-image: url(@/assets/star.png);
  background-repeat: no-repeat;
  background-size: cover;
  top: 5px;
  right: -38px;
}

.price {
  font-size: 28px;
}

.like {
  position: absolute;
  right: 10px;
  top: 10px;
  border: transparent;
  width: 40px;
  height: 40px;
  background-color: transparent;
  cursor: pointer;
}

.like img {
  width: 100%;
  min-height: 100%;
  object-fit: contain;
}

@media(max-width:991px) {


  .title {
    font-size: 30px;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
  }

  .main-image {
    padding: 0 10px;
  }

  .main-image img {
    max-height: 300px;
  }

  .price {
    font-size: 24px;
    margin: 15px 0;
  }

  .rating {
    font-size: 36px;
  }

  .rating::after,
  .country-flag {
    width: 28px;
    height: 28px;
  }

  .country {
    font-size: 23px;
    margin: 5px 0;
    padding-left: 34px;
  }

  @media(max-width:480px) {
    .main-image img {
      max-height: 250px;
    }

    .title {
      font-size: 24px;
      margin: 20px 0;
      max-width: 83%;
    }

    .country,
    .price {
      font-size: 18px;
    }

    .rating {
      font-size: 28px;
    }

    .catalog-item {
      padding: 0;
      margin-bottom: 30px;
    }

    .country-flag,
    .rating::after {
      height: 23px;
      width: 23px;
    }

    .country {
      padding-bottom: 20px;
      padding-left: 28px;
    }

    .catalog-item:hover .main-image img {
      transform: scale(1) rotate(0);
    }

    .barrel {
      bottom: -20%;
    }

    .like {
      right: 3px;
      top: 3px;
    }
  }
}
</style>