<template>
  <div class="container">
    <div class="cart-inner">
      <wineList v-if="allFavList.length > 0" class="cart-list" :allWines="allFavList">
      </wineList>
      <sadCat v-else>
        <h1>Seems like Your top list is empty </h1>
      </sadCat>
    </div>
  </div>
</template>

<script>
import wineList from '@/components/wineList.vue';
import wineCard from '@/components/wineCard.vue';
import sadCat from '@/components/base/sadCat.vue';

import { mapGetters } from 'vuex';

export default {
  components: {
    wineList,
    wineCard,
    sadCat
  },
  computed: {
    ...mapGetters(['allFavList'])
  }
}
</script>

<style >
.cart-inner {
  padding: 80px 0;
}

.cart-list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
  width: 100%;
  padding-left: 0;
}

.cart-list .catalog-item {
  padding: 25px 10px;
  margin-bottom: 0;
}



.cart-list .barrel {
  display: none;
}

.cart-list .title {
  font-size: 22px;
  margin: 15px 0;
}

.cart-list .country {
  margin: 0;
  max-width: 100%;
  font-size: 22px;
}

.cart-list .rating {
  font-size: 32px;
  margin-top: 5px;
}

.cart-list .rating::after {
  top: 0;
}

.cart-list .price {
  margin: 0;
  font-size: 24px;
}

.cart-list .like {
  top: 0;
  right: 0;
}
.cart-inner .undefined{
  width: 100%;
}

@media(max-width:1180px) {
  .cart-list {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media(max-width:768px) {
  .cart-list {
    grid-template-columns: repeat(1, 1fr);
  }
}

@media(max-width:480px) {
  .cart-list .like {
    top: -10px;
  }

  .cart-list .catalog-item {
    padding: 12px 5px;
  }
}
</style>