<template>
  <div class="filter">
    <form class="form-filter">
      <h3>Find Your Wine</h3>
      <div class="filter-item">
        <label class="search-name">
          <input @input="searchByName($event)" type="text" placeholder="Search by name">
        </label>
      </div>
      <div class="filter-item">
        <span class="filter-price">Search by price</span>
        <label class="search-name">
          <input @input="searchMinPrice($event)" type="number" placeholder="Starts from $">
        </label>
        <label class="search-name">
          <input @input="searchMaxPrice($event)" type="number" placeholder="Ends to $">
        </label>
      </div>
      <div class="filter-item">
        <label>
          <select @change="searchByCountry($event)">
            <option value="" selected>Search by country</option>
            <option :value="country" v-for="country in countryList" :key="country">{{ country }}
            </option>
          </select>
        </label>
      </div>
      <div class="filter-item">
        <label>
          <select @change="filterByRating($event)">
            <option value="0" selected>Search by Rating</option>
            <option value="1">Highest</option>
            <option value="2">Lowest</option>
          </select>
        </label>
      </div>
    </form>
  </div>
</template>
<script>

export default {
  props: {
    name: {
      type: String,
      default: ''
    },
    minPrice: {
      type: Number,
      default: 0
    },
    maxPrice: {
      type: Number,
      default: 0
    },
    country: {
      type: String,
      default: ''
    },
    rating: {
      type: Number,
      default: 0
    },
    allWines: {
      type: Array
    }
  },
  methods: {
    searchByName(e) {
      this.$emit('update:name', e.target.value)
    },
    searchMinPrice(e) {
      this.$emit('update:minPrice', e.target.value)
    },
    searchMaxPrice(e) {
      this.$emit('update:maxPrice', e.target.value)
    },
    searchByCountry(e) {
      this.$emit('update:country', e.target.value)
    },
    filterByRating(e) {
      this.$emit('update:rating', Number(e.target.value))
      console.log(Number(e.target.value));
    }
  },
  computed: {
    countryList() {
      return Array.from(new Set(this.allWines.map(item => item.country)));
    }
  }
}
</script>

<style  scoped>
.filter {
  width: 26%;
}

h3 {
  font-size: 34px;
  margin-bottom: 50px;
}

form {
  padding: 20px 20px 40px;
  border-radius: 15px;
  background-color: #998431;
  position: relative;
}

form::after {
  position: absolute;
  content: '';
  width: 40px;
  height: 40px;
  background-repeat: no-repeat;
  background-size: cover;
  top: 10px;
  right: 10px;
  background-image: url(@/assets/grape.svg);
}

label {
  width: 100%;
  display: inline-block;
}

input {
  margin-bottom: 30px;
  font-size: 20px;
}

select {
  width: 100%;
  padding: 20px 12px;
  background: #FFFFFf;
  border-radius: 10px;
  font-weight: 500;
  font-size: 16px;
  cursor: pointer;
  color: #747474;
  min-height: 63px;
  margin-bottom: 30px;
}

.search-name input {
  width: 100%;
  box-sizing: border-box;
  border: 2px solid #ccc;
  border-radius: 8px;
  font-size: 16px;
  background-color: white;
  padding: 20px 12px;
  transition: width 0.4s ease-in-out;
  outline: #C4C4C4;
}

.filter-price {
  font-size: 22px;
  display: inline-block;
  margin-bottom: 10px;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  /* display: none; <- Crashes Chrome on hover */
  -webkit-appearance: none;
  margin: 0;
  /* <-- Apparently some margin are still there even though it's hidden */
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

input[type='number'] {
  -moz-appearance: textfield;
}

@media(max-width:991px) {
  h3 {
    font-size: 22px;
  }

  form {
    padding: 20px 10px 40px;
  }

  form::after {
    height: 30px;
    width: 30px;
  }

  .search-name input {
    padding: 15px 12px;
  }
}
@media(max-width:768px){
  .filter{
    width: 100%;
  }
  h3{
    margin-bottom: 30px;
    margin-top: 10px;
  }
  form{
    padding-bottom: 20px;
    margin-bottom: 30px;
  }
}
@media(max-width:480px){
.search-name input,
select{
  font-size: 14px;
}
}
</style>