import axios from 'axios';
export default {
  state: {
    coloredWine: [],
    listLoading: false
  },
  getters: {
    allColoredWine(state) {
      return state.coloredWine ? state.coloredWine : []
    },
    isListLoading(state){
      return state.listLoading
    }
  },
  mutations: {
    updateColoredWine(state, coloredWine) {
      state.coloredWine = coloredWine
    },
    updateListLoading(state) {
      state.listLoading = !state.listLoading
    }
  },
  actions: {
    loadColoredWine(context, { color }) {
      context.commit('updateListLoading')
      const url = new URL('https://648044caf061e6ec4d48ef0e.mockapi.io/api/wine/wines');
      url.searchParams.append('color', color);
      axios.get(url)
        .then(resp => {
          context.commit('updateColoredWine', resp.data)
          context.commit('updateListLoading')
        })
        .catch((e) => {
          alert(e)
        })
    }
  }
}