import axios from 'axios';
export default {
  state: {
    wine: {},
    wineLoading: false
  },
  getters: {
    getCurWine(state) {
      return Object.keys(state.wine).length !== 0 ? state.wine : {}
    },
    isWineLoading(state) {
      return state.wineLoading
    }
  },
  mutations: {
    changeDetailWine(state, wine) {
      state.wine = wine
    },
    updateWineLoading(state) {
      state.wineLoading = !state.wineLoading
    }
  },
  actions: {
    getWineByID(context, { id }) {
      context.commit('updateWineLoading')
      axios.get('https://648044caf061e6ec4d48ef0e.mockapi.io/api/wine/wines')
        .then(resp => resp.data.find(item => item.id == id))
        .then(resp => context.commit('changeDetailWine', resp))
        .then(() => context.commit('updateWineLoading'))
        .catch((e) => {
          alert(e)
        })
    }
  }
}