<template>
  <div class="container">
    <div class="catalog-inner">
      <wineFilter v-model:name="name" v-model:minPrice="priceStart" v-model:maxPrice="priceEnd" v-model:country="country"
        v-model:rating="rating" :allWines="filteredWineList" />

      <preloaderVue v-if="isListLoading" />

      <wineList class="catalog-list" :allWines="winePaginate"
        v-else-if="!isListLoading && filteredWineList.length !== 0" />

      <sadCat v-if="!isListLoading && filteredWineList.length == 0">
        <h1>Sorry, we can't find this wine</h1>
      </sadCat>


    </div>
    <winePagination v-model="page" :wineAmount="filteredWineList.length" :perPage="limit" />
  </div>
</template>
<script>

import wineFilter from '@/components/wineFilter.vue';
import wineList from '@/components/wineList.vue';
import winePagination from '@/components/winePagination.vue';
import preloaderVue from '@/components/base/preloaderVue.vue';
import sadCat from '@/components/base/sadCat.vue';

import { mapGetters } from 'vuex';
export default {
  data() {
    return {
      page: 1,
      limit: 5,
      name: '',
      priceStart: 0,
      priceEnd: 0,
      country: '',
      rating: 0
    }
  },
  components: {
    wineFilter,
    wineList,
    winePagination,
    preloaderVue,
    sadCat
  },
  methods: {
    loadCards() {
      this.$store.dispatch('loadColoredWine', { color: this.$route.params.id })
    }
  },
  created() {
    this.loadCards()
  },
  computed: {
    ...mapGetters(['allColoredWine', 'isListLoading']),
    winePaginate() {
      const offset = (this.page - 1) * this.limit
      return this.filteredWineList.slice(offset, offset + this.limit)
    },
    filteredWineList() {
      let wines = this.allColoredWine
      if (this.priceStart) {
        wines = wines.filter(item => item.price > this.priceStart)
      }
      if (this.priceEnd) {
        wines = wines.filter(item => item.price < this.priceEnd)
      }
      if (this.country) {
        wines = wines.filter(item => item.country == this.country)
      }
      if (this.rating) {
        if (this.rating === 1) {
          wines.sort((a, b) => b.points - a.points)
        }
        if (this.rating === 2) {
          wines.sort((a, b) => a.points - b.points)
        }
      }
      if (this.name) {
        wines = wines.filter(item => {
          return item.title.toLowerCase().indexOf(this.name.toLowerCase()) !== -1
        })
      }
      return wines
    }
  },
  watch: {
    page() {
      window.scrollTo(0, 0);
    },
    country() {
      this.page = 1
    },
    name() {
      this.page = 1
    },
    rating() {
      this.page = 1
    },
    priceStart() {
      this.page = 1
    },
    priceEnd() {
      this.page = 1
    }

  }
}
</script>

<style  scoped>
.catalog-list {
  width: 70%;
}

.catalog-inner {
  padding: 100px 0;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
}

@media(max-width:991px) {
  .catalog-list {
    padding-left: 30px;
  }
}

@media(max-width:768px) {
  .catalog-inner {
    flex-direction: column;
    padding: 50px 0;
  }

  .catalog-list {
    width: 100%;
    padding-left: 0;
  }
}
</style>